import React, {Component} from 'react';
import {Card, Icon, Avatar} from 'antd';

import {importAll} from "utils";


class CardSmall extends Component {
    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        return (
            <div className="card--small">
                <div className="card--small_img_svg" style={{
                    backgroundImage: `url(${images['card_small_u596.svg']})`,
                    // height: '0.6rem',
                    // width: '0.6rem'
                }}></div>

                <div className="card--small__content">
                    <div className="card--small__content-title">
                        [TRAINER CD] Ro-Ghoul [ALPHA]
                    </div>
                    <div className="card--small__content-comment">
                        <img className="card--small__content-comment_img"
                             src={images['thumb_up_u608.png']}/>
                        <div className="card--small__content-comment_number">
                            2.1k
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default CardSmall;
