import React, {Component} from 'react';
import {Alert} from 'antd';
import Header from 'components/partial/header/Header';
import Carousel from "components/partial/carousel/Carousel";
import Search from "components/partial/search/Search";
import CardBig from "components/partial/card/CardBig";

import {importAll} from "utils";
import CardSmall from "components/partial/card/CardSmall";
import CardMid from "components/partial/card/CardMid";

class Home extends Component {

    constructor(props) {
        super(props);
        console.log(this.props);
        this.static_server = 'http://www.wulihub.com.cn/go/Qxa7kJ/';
    }


    onClose = e => {
        console.log(e, 'I was closed.');
    };

    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        return (
            <>
                <Header/>
                <div className="main">
                    <Carousel/>
                </div>
                <div className="sections">
                    <Search phone={true}/>

                    <div className="recent-comments">
                        <div className="category">
                            <span className="category-title">近期好评</span>
                            <a className="category-more">
                                更多
                            </a>
                        </div>
                        <div className="game-list--big">
                            {
                                new Array(3).fill(1).map((item, index) => (
                                    <CardBig/>
                                ))
                            }
                        </div>
                        <div className="game-list--small">
                            {
                                new Array(6).fill(1).map((item, index) => (
                                    <CardSmall/>
                                ))
                            }
                        </div>
                    </div>


                    <div className="new-games">
                        <div className="category">
                            <span className="category-title">最新游戏</span>
                            <a className="category-more">
                                更多
                            </a>
                        </div>

                        <div className="game-list--mid">
                            {
                                new Array(6).fill(1).map((item, index) => (
                                    <CardMid/>
                                ))
                            }
                        </div>
                    </div>

                    <div className="recommends">
                        <div className="category">
                            <span className="category-title">向您推荐</span>
                            <a className="category-more">
                                更多
                            </a>
                        </div>
                        <div className="game-list--small">
                             {
                                new Array(12).fill(1).map((item, index) => (
                                    <CardSmall/>
                                ))
                            }
                        </div>
                    </div>

                     <div className="discussions">
                        <div className="category">
                            <span className="category-title">讨论最多</span>
                            <a className="category-more">
                                更多
                            </a>
                        </div>
                      <div className="game-list--small">
                             {
                                new Array(12).fill(1).map((item, index) => (
                                    <CardSmall/>
                                ))
                            }
                        </div>
                     </div>

                    <div className="hot-games">
                        <div className="category">
                            <span className="category-title">热门游戏</span>
                            <a className="category-more">
                                更多
                            </a>
                        </div>
                        <div className="game-list--small">
                             {
                                new Array(12).fill(1).map((item, index) => (
                                    <CardSmall/>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

export default Home;