import React, {Component} from 'react';
import {Card, Icon, Avatar} from 'antd';

import {importAll} from "utils";

const {Meta} = Card;

class CardBig extends Component {
    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        return (
            <Card
                className="card--big"
                cover={
                    <img
                        alt="example"
                        src={images['carousel_item_u587.svg']}
                    />
                }
            >
                {/*<Meta*/}
                {/*    // avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png"/>}*/}
                {/*    title="Card title"*/}
                {/*    description="This is the description"*/}
                {/*/>*/}
                <div className="card--big__content">
                    <div className="card--big__content-left">
                        <div className="card--big__content-title">
                            [TRAINER CD] Ro-Ghoul [ALPHA]
                        </div>
                        <div className="card--big__content-author">
                            <span>By</span>
                            SushiWalrus
                        </div>
                    </div>
                    <div className="card--big__content-comment">
                        <img className="card--big__content-comment_img"
                             src={images['thumb_up_u608.png']}/>
                        <div className="card--big__content-comment_number">
                            2.1k
                        </div>
                    </div>
                </div>
            </Card>
        )
    }
}

export default CardBig;