import React, {Component} from 'react';
import {Card, Icon, Avatar, Skeleton} from 'antd';

import {importAll} from "utils";

const {Meta} = Card;

class CardMid extends Component {
    state = {
        loading: true
    }

    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        return (
            <Card
                className="card--mid"
                hoverable
                cover={<img alt="example" className="card--mid__img--svg" src={images['card_mid_u573.svg']} />}
            >

                    {/*<img className="card--mid__img--svg"*/}
                    {/*     src={images['card_mid_u573.svg']}/>*/}
                    <div className="card--mid__content">
                        <div className="card--mid__content-title">
                            [TRAINER CD] Ro-Ghoul [ALPHA]
                        </div>
                        <div className="card--mid__content-comment">
                            <img className="card--mid__content-comment_img"
                                 src={images['clock_u630.png']}/>
                            <div className="card--mid__content-comment_number">
                                2019-5-23 16:14
                            </div>
                        </div>
                    </div>
            </Card>
        )
    }
}

export default CardMid;
