import React, {Component} from 'react';
import ReactDOM from 'react-dom';
// import {Carousel, Radio} from 'antd';
import { Carousel } from 'antd-mobile';
import { Link, withRouter } from 'react-router-dom';
import {importAll} from 'utils';

class CarouselIndex extends Component {
    state = {
        data: ['1', '2', '3'],
        imgHeight: 176,
        slideIndex: 2,
    }
    componentDidMount() {
        setTimeout(() => {
            this.setState({
                data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
            });
        }, 100);
    }

    componentDidUpdate() {
        // After the new child element is rendered, change the slideIndex
        // https://github.com/FormidableLabs/nuka-carousel/issues/327
        if (this.state.slideIndex !== this.state.data.length - 1) {
            /* eslint react/no-did-update-set-state: 0 */
            this.setState({ slideIndex: this.state.data.length - 1 });
        }
        console.log(`history: - - - - - -`, this.props.history);
    }

    _toGameDetail = (e, id=1) => {
        console.log(e, id, ' - - - -- -  - - -||||');
        this.props.history.push(`/home/games/${id}`);
    }

    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);


        return (
            <>
                <div className="carousel-web">
                    <Link to={`/home/games/1`}>
                        <div className="carousel-web--active"
                             style={{backgroundImage: `url(${images['carousel_item_u587.svg']})`}} onClick={() => console.log('hahahahaha')}>
                            {/*<img className="carousel-item" src={`${this.static_server}images/游戏大厅/u587.svg`}/>*/}
                        </div>
                    </Link>
                    <div className="carousel-web--normal">
                        <div className="carousel-web-item"
                             style={{backgroundImage: `url(${images['carousel_item_u587.svg']})`}} onClick={(e) =>this._toGameDetail(e,2)}></div>
                        <div className="carousel-web-item"
                             style={{backgroundImage: `url(${images['carousel_item_u587.svg']})`}} onClick={(e) =>this._toGameDetail(e,3)}></div>
                        <div className="carousel-web-item"
                             style={{backgroundImage: `url(${images['carousel_item_u587.svg']})`}} onClick={(e) =>this._toGameDetail(e,4)}></div>
                        {/*<img className="carousel-item" src={images['carousel_item_u587.svg']}/>*/}
                        {/*<img className="carousel-item" src={images['carousel_item_u589.svg']}/>*/}
                        {/*<img className="carousel-item" src={images['carousel_item_u590.svg']}/>*/}
                    </div>
                </div>

                <div className="carousel-dots">

                        <Carousel
                            autoplay={false}
                            infinite
                            dots={true}
                            selectedIndex={this.state.slideIndex}
                            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                            afterChange={index => console.log('slide to', index)}
                        >
                            {this.state.data.map((val, index) => (
                                <a
                                    key={val + index}
                                    href="http://www.alipay.com"
                                    style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                                >
                                    <img
                                        src={`${images['carousel_item_u587.svg']}`}
                                        alt=""
                                        style={{ width: '100%', verticalAlign: 'top' }}
                                        onLoad={() => {
                                            // fire window resize event to change height
                                            window.dispatchEvent(new Event('resize'));
                                            this.setState({ imgHeight: 'auto' });
                                        }}
                                    />
                                </a>
                            ))}
                        </Carousel>
                </div>
            </>
        );
    }
}

export default withRouter(CarouselIndex);